<template>
  <div id="app">
    <myheader/>
    <div id="content">
      <div class="router">
          <router-view/>
      </div>
      <myfooter></myfooter>
    </div>
  </div>
</template>

<script>
    import myheader from './components/header/myheader.vue'
    import myfooter from './components/footer/myfooter.vue'
export default {
    name: 'App',
    components:{
        myheader,
        myfooter
    }
}
</script>

<style lang="less" rel='stylesheet/less'>
#content{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top:0px;
    background: #333;
    overflow: auto;
    z-index: -1;
    .router{
        min-height: 100%;
        padding-top: 71px;
    }
}
</style>



